import React from 'react';
import { Modal, Button, Input } from 'antd';
const Search = Input.Search;

class WordItem extends React.Component {  
  constructor(props) {
    super(props);
  }
  render() {
    const word = this.props.value;
    return <li>{word.word + " / " + ((word.hyphenation == null || word.hyphenation === "") 
                ? "" : word.hyphenation) + "  " 
                + ((word.phone == null || word.phone === "") ? "": ("[" + word.phone + "]"))+ "   "}
                {
                  this.props.onClick == null ? "":
                  <Button icon="close" size="small" onClick={this.props.onClick.bind(this, word)}/>
                  }
        <ul>
          {(word.mean.map((mean) =>
            <li>{mean.value}</li>
          ))}
        </ul>
      </li>;
  }
}

class WordList extends React.Component {  
  constructor(props) {
    super(props);
  }
  render() {
    const words = this.props.words;
    return (
      <ul>
        {words.map((word) =>
          <WordItem key={word.word} value={word} onClick={this.props.onClick} />
        )}
      </ul>
    );
  }
}

class App extends React.Component {
  state = {
    initLoading: true,
    loading: false,
    words: [],
    article: "001",
    visible:false,
    word:null
  }

  componentDidMount() {
    this.getWords("001");
  }

  getWords(article) {
    var str = 'http://localhost:44430/getwordsbyarticle/';
    str += article;

    const request = new Request(str, {method: 'get'});
    fetch(request)
      .then(response => {
        if (response.status === 200) {
          return response.json();
        } else {
          throw new Error('Something went wrong on api server!');
        }
      })
      .then(response => {
        console.debug(response);
        this.setState({
          initLoading: false,
          words: response.words,
        });
      }).catch(error => {
        console.error(error);
      });
  }

  getWord(word) {
    var str = 'http://localhost:44430/getword/';
    str += word;
    str += "/";
    str += this.state.article;
    str += "/0";

    const request = new Request(str, {method: 'get'});
    fetch(request)
    .then(response => {
      if (response.status === 200) {
        return response.json();
      } else {
        throw new Error('Something went wrong on api server!');
      }
    })
    .then(response => {
      this.setState({
        visible: true,
        word: response,
      });
    }).catch(error => {
      console.error(error);
    });
  }

  onDelete = (item, e) => {
    var url = 'http://localhost:44430/changenewflag/';
    url += item.word;
    url += '/0';
    console.debug(url);
    const request = new Request(url, {method: 'get'});
    fetch(request)
      .then(response => {
        if (response.status === 200) {
          return response.json();
        } else {
          throw new Error('Something went wrong on api server!');
        }
      })
      .then(response => {
        if (response.result === 0) {
          var words = this.state.words;
          for (var i=0; i<words.length; i++) {
            if (words[i].word === item.word) {
              words.splice(i,1);
              break;
            }
          }
          this.setState(({
              words: words,
            })
          );
        }
      }).catch(error => {
        console.error(error);
      });
  }

  onPressEnter = (e) => {
    this.setState({
      article:e.target.value,
    });
    this.getWords(e.target.value);    
  }

  onSearch = (word) => {
    this.getWord(word);
  }

  handleOk = (e) => {
    var words = this.state.words;
    for (var i=0; i<words.length; i++) {
      if (words[i].word === this.state.word.word) {
        words.splice(i,1);
        break;
      }
    }
    words.splice(0, 0, this.state.word);

    this.setState({
      words: words,
      visible: false,
    });

    var str = 'http://localhost:44430/getword/';
    str += this.state.word.word;
    str += "/";
    str += this.state.article;
    str += "/1";
    console.error(str);
    const request = new Request(str, {method: 'get'});
    fetch(request);
  }

  handleCancel = (e) => {
    this.setState({
      visible: false,
    });
  }

  render() {
    const { words } = this.state;
    return (
      <div>
        <Input defaultValue="001" onPressEnter = {this.onPressEnter} />
        
        <Search
          placeholder="input english word"
          enterButton="Search"
          size="snall"
          onSearch={this.onSearch}
        />
        <WordList words={words} onClick={this.onDelete} >
        </WordList>

        <Modal
          title={this.state.word == null ? "null": this.state.word.word}
          style={{ top: 20}}
          width="100%"
          visible={this.state.visible}
          onOk={this.handleOk}
          onCancel={this.handleCancel}
        >
          {
            this.state.word == null ? "":
            <WordItem key={this.state.word.word} value={this.state.word} />
          }
        </Modal>
      </div>
    );
  }
}

export default App;


